@import "variables";

@media screen and (max-width: 1500px), screen and (max-device-width: 1500px) {
  body {
    font-size: 16px;
    line-height: 18px;
    .header {
      .container {
        .header__mainSearch {
          .search {
            margin-top: 40px;
            i{
              top: 15px;
            }
          }
          .numbers {
            margin: 25px 0 0;
          }
          .workTime {
            margin: 30px 0 0;
          }
        }
        .header__cabinet {
          margin-top: 40px;
          ul {
            li {
              &:nth-child(3) {
                a{
                  i {
                    margin: 0 !important;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1310px), screen and (max-device-width: 1310px){
  body{
    font-size: 14px;
    line-height: 16px;
    .header{
      .container{
        .header__headline{
          p{
            font-size: .875em;
          }
        }
        .header__logo{
          a,
          span{
            margin-left: 0;
          }
        }
        .header__mainSearch {
          .search{
            input{
              height: 30px;
              line-height: 30px;
            }
            i{
              top: 11px;
              right: 15px;
              font-size: 1.25em;
            }
          }
          .numbers{
            ul{
              li{
                .icon-auricular-phone-symbol-in-a-circle{
                  font-size: 2em;
                }
              }
            }
          }
          .workTime{
            ul{
              margin-left: 10px;
            }
          }
        }
        .header__cabinet{
          ul{
            li{
              a{
                span{
                  i{
                    padding: 0 10px;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1150px), screen and (max-device-width: 1150px){
  body{
    font-size: 12px;
    line-height: 14px;
    .header{
      .container{
        grid-template-areas: "headline headline headline"
                             "logo search cabinet"
                             "navPanel navPanel navPanel"
                             "secondNav secondNav secondNav";
        .header__logo{
          margin-top: 20px;
        }
        .header__mainSearch{
          .search{
            margin-top: 20px;
            input{
              width: 90%;
            }
          }
          .numbers{
            margin: 15px 0 0;
            .icon-auricular-phone-symbol-in-a-circle{
              margin: 0 0 0 5px;
              display: block;
              font-size: 3em;
              color: @mainColor;
            }
            ul{
              width: unset;
              flex-direction: column;
              li{
                margin: 0 0 0 15px;
                .icon-auricular-phone-symbol-in-a-circle{
                  display: none;
                }
                a{
                  font-size: 1.375em;
                  line-height: 1.5em;
                }
              }
            }
          }
          .workTime{
            margin: 20px 0 0 0;
          }
        }
        .header__cabinet{
          margin-top: 25px;
          ul{
            li{
              a{
                span{
                  i{
                    padding: 0 5px;
                  }
                }
              }
              &:nth-child(3){
                a{
                  margin-top: 25px;
                }
              }
            }
          }
        }
        .header__nav-panel{
          &__major,
          &__goods{
            ul{
              li{
                a{
                  line-height: 35px;
                }
                span{
                  line-height: 35px;
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 870px), screen and (max-device-width: 870px){
  body{
    .header{
      .container{
        .header__cabinet{
          ul{
            flex-direction: column;
            li{
              width: 100%!important;
              a{
                justify-content: unset;
                span{
                  i{
                    padding: 0 10px;
                  }
                  &:nth-child(2){
                    white-space: pre-wrap;
                  }
                }
              }
              &:nth-child(2){
                margin: 0 0 0 10px;
              }
              &:nth-child(3){
                a{
                  margin-top: 0;
                  width: 85%;
                }
              }
            }
          }
        }
        .header__nav-panel{
          &__major,
          &__goods{
            ul{
              li{
                a{
                  font-size: 1em;
                }
                span{
                  font-size: 1em;
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 740px), screen and (max-device-width: 740px){
  body{
    .header{
      .container{
        .header__mainSearch{
          flex-direction: column;
          .numbers,
          .workTime{
            margin: 10px 0 0;
            justify-content: center;
            ul{
              display: flex;
              flex-direction: row;
              justify-content: center;
              align-items: center;
            }
          }
          .workTime{
            ul{
              li{
                margin: 0 20px 0 0;
              }
            }
          }
        }
        .header__cabinet{
          ul{
            li{
              &:nth-child(3){
                margin: 0 0 0 10px;
                a{
                  width: 100%;
                  span{
                    i{
                      font-size: 2.5em;
                    }
                  }
                }
              }
            }
          }
        }
        .header__nav-panel{
          align-items: center;
          .mobileDrop{
            display: flex;
            justify-content: center;
            align-items: center;
            width: 90%;
            font: 1.25em/2em @sansbold;
            color: @linkColor;
            text-align: center;
            background-color: @mainColor;
            border-radius: 5px;
            text-decoration: none;
            &:hover{
              cursor: pointer;
            }
          }
          &__major,
          &__goods {
            display: none;
          }
          &__mobile{
            width: 300px;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 680px), screen and (max-device-width: 680px){
  body{
    .header{
      .container{
        grid-template-areas: "headline headline"
                             "search search"
                             "cabinet cabinet"
                             "navPanel navPanel"
                             "secondNav secondNav";
        grid-template-columns: 30% 70%;
        .header__headline{
          p{
            font-size: .75em;
            line-height: 2em;
          }
        }
        .header__logo{
          display: none;
        }
        .header__mainSearch{
          .search{
            i{
              right: 25px;
            }
          }
          .numbers,
          .workTime{
            width: 100%;
          }
          .workTime{
            i{
              margin: 0 20px;
            }
          }
        }
        .header__cabinet{
          ul{
            flex-direction: row;
            justify-content: center;
            li{
              margin: 0 20px!important;
              width: unset!important;
              a{
                justify-content: center;
                border: none!important;
                span{
                  i{
                    margin: 0;
                    padding: 0;
                    border: none;
                  }
                  &:nth-child(2){
                    display: none;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}